.discountBlock {
    display: flex;
    justify-content: space-around;
    flex-direction: row;
    background-color: #D52027;
    color: #fff;
    padding: 30px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}
.discountInfo h2 {
    font-size: 24px;
    width: 60%;
}

.discountInfo p {
    font-size: 16px;
    width: 60%;
}

.discountForm {

    padding-left: 20px;
}

.discountForm p {
    font-size: 14px;
    opacity: 0.7;
}

.discountForm form {
    margin-top: 20px;
}

.discountForm input {
    width: 60%;
    padding: 10px;
    margin-bottom: 10px;
    border: none;
    border-radius: 4px;
}

.discountForm button {
    background-color: #141024;
    color: #fff;
    padding: 8px 16px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    width: 63%;
}
.discountForm {
    display: flex;
    justify-content: flex-end;
    opacity: 0;
    transform: translateX(20px);
    transition: opacity 0.5s ease, transform 1.5s ease;
}

.discountForm.active {
    animation: slideIn 1s ease-in-out forwards;
}

@keyframes slideIn {
    from {
        transform: translateX(100%);
        opacity: 0;
    }
    to {
        transform: translateX(0);
        opacity: 1;
    }
}
@media (max-width: 768px) {
    .discountBlock {
        display: flex;
        justify-content: space-between;
        flex-direction: row;
        padding: 30px;

    }

    .discountInfo h2 {
       width: 100%;
        font-size: calc(15px + 2 * ((100vw - 320px) / 448));

    }
    .discountInfo p {
        font-size: calc(10px + 2 * ((100vw - 320px) / 448));
    }

    .discountForm {
        justify-content: flex-end;
        opacity: 0;
        transform: translateX(20px);
        transition: opacity 0.5s ease, transform 1.5s ease;
    }

    .discountForm.active {
        animation: slideIn 1s ease-in-out forwards;
    }

}



