.infoEnd {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    font-size: 10px;
    gap: 40px;
}
.infoEnd span {
    font-size: 15px;
}
@media screen and (max-width: 768px) {
    .infoEnd {
        flex-direction: column;
        align-items: center;
        margin-left: 10px;
        display: block;
    }
    .BlockIn {
        margin-left: 10px;
    }
    .infoEnd span {
        font-size: calc(12px + 2 * ((100vw - 320px) / 448));

    }
    .infoEnd h1 {
        font-size: calc(15px + 2 * ((100vw - 320px) / 448));

    }
}